/*  cover standards  */

section#cover{
    background-color: $cover-color;
    background-size: 100% auto;
    color: #fff;
    font-family: Raleway, sans-serif;
    width: 8in;
    height: 10in;
    position: relative;
}
#cover div#cover-logo {
    background: url(../images/svg/sitepoint-white.svg) no-repeat;
    position: absolute;
    top: .75in;
    right: 4em;
    width: 2in;
    height: 0.5in;
}

#cover h1.title {
    font-size: 75pt;
    font-weight: normal;
    line-height: 60pt;
    color: #fff;
    letter-spacing: -2px;
    text-align: right;
    text-transform: uppercase;
    margin: 2.2in .2em 0 0;
    position: absolute;
}

#cover h3.tagline {
    font-size: 15pt;
    font-weight: 700;
    line-height: 1.5;
    color: #fff;
    position: absolute;
    text-transform: uppercase;
    top: 8.75in;
    width: 7.9in;
    text-align: center;
}

/*  feature boxes  */

.box {
    background-color: color(backgrounds, box-body);
    margin: 1.5rem 0 1.5rem 0rem;
    position: relative;
    z-index: 1;
    border: 1px color(backgrounds, box-title) solid;
    border-width: 0 1px 1px 1px;
    border-radius: 25px;
}

.box h4 {
    background-color: color(backgrounds, box-title);
    //font-size: 12pt;
    padding: $title-padding;
    margin: 1.5rem 0 0.75rem 0;
    line-height: 1.95rem;
    border-radius: 25px;
    color: #345;
}

.box .body {
    font-size: 9pt;
    padding: 0 2rem;
    
}

.box .body p {
    margin: 0;
    padding: 0 0 0.75rem 0;
    line-height: 1.5rem;
    font-weight: 300;
}

.box.tip h4,
.box.codepen h4,
.box.note h4,
.box.attention h4,
.box.warning h4 {
    padding-left: 3rem;
}

.box.tip .body,
.box.note .body,
.box.codepen .body,
.box.attention .body,
.box.warning .body {
    padding-left: 3rem;
}

.box.tip:before {
    background: url(../images/box-tip.svg);
    @include box-icon;
}

.box.note:before {
    background: url(../images/box-note.svg);
    @include box-icon;
}

.box.attention:before {
    background: url(../images/box-attention.svg);
    @include box-icon;
}

.box.warning:before {
    background: url(../images/box-warning.svg);
    @include box-icon;
}

.box.codepen:before {
    background: url(../images/box-codepen.svg);
    @include box-icon;
}


figure{
    margin: 0;
    -webkit-margin-before: 0em;
    -webkit-margin-after: 0em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    margin-bottom: 1.5rem;
}
