/* tables */
table {
    border-collapse: collapse;
    margin-top: 1em;
}

table caption {
    font-weight: bold;
    text-align: left;
    font-size: 12pt;
    padding-bottom: 5pt;
}

table tbody{
    border-bottom: 1px solid #ccc;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
}

table thead{
    border-top: 1px solid #ccc;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
}

th {
    background-color: color(backgrounds, table-header);
    padding: 0.3em;
    text-align: left;
}

td {
    padding: 0.3em;
    vertical-align: top;
}

.table-striped tr:nth-child(2n){
    background-color: color(backgrounds, table-row);
}

/* figures*/
img {
  max-width: 100%;
  //max-height: $page-height - 50pt;
}

.imagefig {
    text-align: center;

}

.imagefig img {
    display: block;
    max-width: 100%;
}

.imagefig figcaption {
    font-size: 8pt;
    margin-bottom: 1.5rem;
    line-height: 1.5rem;
    font-family: "Libre Franklin";
}

/* other images */

.image-inline {
    max-width: 100%;
}

.image-left {
    float: left;
    margin: 0 10pt 10pt 0;
}

.image-right {
    float:right;
    margin: 0 0 10pt 10pt;
}

.image-center {
    display: block;
    margin: 0 auto;
}

.image-left + p, .image-right + p {
    margin-top: 0;
}

.block{
    page-break-inside: avoid;
    page-break-after: always;
}

.breakbefore{
    page-break-before: always;
    break-before: always
}



/* blockquotes */
blockquote {
    background: #f0f2f5;
    margin: 0 0 1.5rem;
    padding: 1.5rem 2em;
    letter-spacing: 1px;
    border-left: 5px solid #c6c8dd;
    p {
        color: #505275;
        font-family: Georgia, Times, "Times New Roman", serif;
        font-style: italic;
        margin-bottom: 0;
        font-weight: 300;
    }
}
.chapter p code, .chapter li code{
    font-style: italic;
    background: #f5f2f0;
    font-size: 0.9rem;
    padding: 0 5px;
}
table {
    width:95%
}